import React, { useState, useEffect } from 'react';
import { Monitor, Smartphone, AlertTriangle, Zap } from 'lucide-react';

// 优化的设备检测工具函数
const DeviceDetector = {
  // 更精确的移动设备检测
  isMobile: () => {
    const userAgent = navigator.userAgent.toLowerCase();
    const mobileKeywords = /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini|mobile/i;
    
    // 综合判断：用户代理 + 屏幕尺寸 + 触摸支持
    const hasSmallScreen = window.innerWidth <= 420; // 调整为420px更合理
    const isMobileUA = mobileKeywords.test(userAgent);
    const hasTouchScreen = 'ontouchstart' in window || navigator.maxTouchPoints > 0;
    
    // 排除iPad等大屏设备的特殊情况
    const isIPad = /ipad/i.test(userAgent) && window.innerWidth > 768;
    
    return (isMobileUA && hasSmallScreen) || (hasSmallScreen && hasTouchScreen) && !isIPad;
  },
  
  // 平板检测（iPad或Android平板）
  isTablet: () => {
    const userAgent = navigator.userAgent.toLowerCase();
    const isIPad = /ipad/i.test(userAgent);
    const isAndroidTablet = /android/i.test(userAgent) && !/mobile/i.test(userAgent);
    
    return isIPad || isAndroidTablet;
  },
  
  // 检测是否是小屏幕但可能是桌面浏览器窗口
  isSmallDesktop: () => {
    const width = window.innerWidth;
    const userAgent = navigator.userAgent.toLowerCase();
    const isMobileUA = /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini|mobile/i.test(userAgent);
    
    // 宽度在420-1200之间，且不是移动设备
    return width >= 420 && width < 1200 && !isMobileUA;
  },
  
  getDeviceType: () => {
    if (DeviceDetector.isMobile()) return 'mobile';
    if (DeviceDetector.isTablet()) return 'tablet';
    if (DeviceDetector.isSmallDesktop()) return 'small-desktop';
    return 'desktop';
  },
  
  getScreenInfo: () => {
    return {
      width: window.innerWidth,
      height: window.innerHeight,
      deviceType: DeviceDetector.getDeviceType(),
      userAgent: navigator.userAgent
    };
  },
  
  // 判断是否需要显示提示 - 方案A：统一强提醒
  shouldShowWarning: () => {
    // 所有小屏幕都显示警告，不管是移动设备还是桌面窗口
    return window.innerWidth < 1200;
  }
};

// 统一的小屏幕警告页面
const SmallScreenWarningPage = ({ onContinue, deviceInfo }) => {
  // 根据设备类型确定提醒内容
  const isTrueDevice = deviceInfo.deviceType === 'mobile' || deviceInfo.deviceType === 'tablet';
  const deviceName = deviceInfo.deviceType === 'mobile' ? '📱 手机设备' : 
                     deviceInfo.deviceType === 'tablet' ? '📱 平板设备' : 
                     '💻 桌面浏览器';

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-800 to-slate-900 flex items-center justify-center p-4">
      <div className="max-w-lg w-full bg-white/95 backdrop-blur-sm rounded-2xl shadow-xl border border-gray-200">
        <div className="p-6 text-center">
          {/* 简洁的标题 */}
          <div className="mb-6">
            <div className="w-16 h-16 bg-blue-500 rounded-full flex items-center justify-center mx-auto mb-4">
              <Monitor className="w-8 h-8 text-white" />
            </div>
            <h1 className="text-2xl font-bold text-gray-800 mb-2">
              屏幕尺寸提醒
            </h1>
            <p className="text-gray-600">
              知识图谱可视化系统
            </p>
          </div>

          {/* 设备信息卡片 */}
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-4">
            <div className="flex items-center justify-center mb-3">
              <AlertTriangle className="w-5 h-5 text-blue-600 mr-2" />
              {isTrueDevice ? <Smartphone className="w-5 h-5 text-blue-600" /> : <Monitor className="w-5 h-5 text-blue-600" />}
            </div>
            <h2 className="text-lg font-semibold text-gray-800 mb-3">
              检测到小屏幕访问
            </h2>
            <div className="text-sm text-gray-700 space-y-2">
              <p>设备类型：<span className="font-medium text-blue-700">{deviceName}</span></p>
              <p>屏幕尺寸：<span className="font-medium text-blue-700">{deviceInfo.width} × {deviceInfo.height}px</span></p>
              <p>系统规模：<span className="font-medium text-green-600">351个知识节点，687条连线</span></p>
            </div>
          </div>

          {/* 建议内容 */}
          <div className="bg-gray-50 border border-gray-200 rounded-lg p-4 mb-6">
            <h3 className="text-base font-semibold text-gray-800 mb-3 flex items-center justify-center">
              <Monitor className="w-4 h-4 mr-2" />
              {isTrueDevice ? '建议使用电脑访问' : '建议扩大浏览器窗口'}
            </h3>
            <div className="text-sm text-gray-600 space-y-1">
              {isTrueDevice ? (
                <>
                  <p>• 复制网址到电脑浏览器</p>
                  <p>• 获得完整的缩放和拖拽功能</p>
                  <p>• 享受流畅的性能表现</p>
                </>
              ) : (
                <>
                  <p>• 按 F11 全屏浏览</p>
                  <p>• 拖拽浏览器边缘扩大窗口</p>
                  <p>• 建议宽度 ≥ 1200px 以获得最佳体验</p>
                </>
              )}
            </div>
          </div>

          {/* 操作按钮 */}
          <div className="flex space-x-3 mb-4">
            <button
              onClick={onContinue}
              className="flex-1 bg-gray-600 hover:bg-gray-700 text-white py-3 px-4 rounded-lg transition-colors font-medium flex items-center justify-center"
            >
              <Zap className="w-4 h-4 mr-2" />
              仍要继续
            </button>
            <button
              onClick={() => window.history.back()}
              className="flex-1 bg-blue-600 hover:bg-blue-700 text-white py-3 px-4 rounded-lg transition-colors font-medium"
            >
              返回
            </button>
          </div>

          {/* 底部说明 */}
          <div className="text-xs text-gray-500 border-t border-gray-200 pt-4">
            <p className="mb-2">💡 本系统为专业知识图谱，小屏幕可能影响浏览体验</p>
            <div className="flex items-center justify-center space-x-2">
              <span>Powered by</span>
              <span className="text-blue-600 font-medium">ZWHZ MOA</span>
              <span>|</span>
              <span className="text-purple-600 font-medium">课研AI</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

// 主设备检测组件
const DeviceWarning = ({ children }) => {
  const [deviceInfo, setDeviceInfo] = useState(null);
  const [showWarning, setShowWarning] = useState(false);

  // 检查是否需要显示警告的函数
  const checkShouldShowWarning = (width) => {
    // 如果是大屏幕，不显示警告
    if (width >= 1200) {
      return false;
    }
    
    // 检查用户之前的选择和时间戳
    const savedChoice = localStorage.getItem('device-warning-choice');
    const savedTimestamp = localStorage.getItem('device-warning-timestamp');
    
    // 如果用户之前选择了继续
    if (savedChoice === 'continue' && savedTimestamp) {
      const now = Date.now();
      const saved = parseInt(savedTimestamp);
      // 24小时内不再提示
      if (now - saved < 24 * 60 * 60 * 1000) {
        return false;
      } else {
        // 超过24小时，清除旧记录
        localStorage.removeItem('device-warning-choice');
        localStorage.removeItem('device-warning-timestamp');
      }
    }
    
    // 小屏幕且没有有效的用户选择，显示警告
    return true;
  };

  useEffect(() => {
    const updateDeviceInfo = () => {
      const info = DeviceDetector.getScreenInfo();
      setDeviceInfo(info);
      
      // 实时检查是否需要显示警告
      const shouldShow = checkShouldShowWarning(info.width);
      setShowWarning(shouldShow);
    };

    // 初始检测
    updateDeviceInfo();

    // 监听窗口大小变化，实时响应
    const handleResize = () => {
      updateDeviceInfo();
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleContinue = () => {
    setShowWarning(false);
    // 保存用户选择，24小时内不再提示
    localStorage.setItem('device-warning-choice', 'continue');
    localStorage.setItem('device-warning-timestamp', Date.now().toString());
  };

  if (!deviceInfo) {
    // 加载状态
    return (
      <div className="min-h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-white text-center">
          <div className="animate-spin w-8 h-8 border-2 border-blue-500 border-t-transparent rounded-full mx-auto mb-4"></div>
          <p>正在检测设备...</p>
        </div>
      </div>
    );
  }

  // 所有小屏幕显示警告页面
  if (showWarning) {
    return <SmallScreenWarningPage onContinue={handleContinue} deviceInfo={deviceInfo} />;
  }

  // 大屏幕桌面直接显示
  return children;
};

export default DeviceWarning;
export { DeviceDetector }; 