import React, { useState } from 'react';
import './App.css';
import LayeredKnowledgeGraph from './LayeredKnowledgeGraph';
import DeviceWarning from './components/DeviceWarning';
import GameSplash from './components/GameSplash';

function App() {
  const [showSplash, setShowSplash] = useState(true);

  const handleEnterGraph = () => {
    setShowSplash(false);
  };

  const handleBackToSplash = () => {
    setShowSplash(true);
  };

  return (
    <div className="App">
      <DeviceWarning>
        {showSplash ? (
          <GameSplash onEnter={handleEnterGraph} />
        ) : (
          <LayeredKnowledgeGraph onBackToSplash={handleBackToSplash} />
        )}
      </DeviceWarning>
    </div>
  );
}

export default App; 