import React, { useState, useMemo } from 'react';
import { ChevronDown, ChevronRight, BookOpen, Search, Filter } from 'lucide-react';

// 导入知识分类数据
import knowledgeCategories from '../data/knowledge_categories.json';
import nodesKnow from '../data/nodes_know.json';

const KnowledgeTest = ({ onBackToMain }) => {
  const [expandedGroups, setExpandedGroups] = useState(new Set());
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');

  // 获取所有知识点数据
  const allKnowledgeNodes = useMemo(() => {
    return nodesKnow;
  }, []);

  // 根据分类筛选知识点
  const filteredCategories = useMemo(() => {
    if (selectedCategory === 'all') {
      return knowledgeCategories;
    }
    return knowledgeCategories.filter(cat => cat.chapter === selectedCategory);
  }, [selectedCategory]);

  // 根据搜索词筛选知识点
  const filteredNodes = useMemo(() => {
    if (!searchTerm) return allKnowledgeNodes;
    
    return allKnowledgeNodes.filter(node => 
      node.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      node.description.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [allKnowledgeNodes, searchTerm]);

  // 切换分组展开状态
  const toggleGroup = (chapter) => {
    const newExpanded = new Set(expandedGroups);
    if (newExpanded.has(chapter)) {
      newExpanded.delete(chapter);
    } else {
      newExpanded.add(chapter);
    }
    setExpandedGroups(newExpanded);
  };

  // 获取分组中的知识点
  const getGroupNodes = (category) => {
    const nodeIds = new Set(category.nodes);
    return filteredNodes.filter(node => nodeIds.has(node.id));
  };

  // 获取知识类型颜色
  const getKnowledgeTypeColor = (type) => {
    const colors = {
      '专业知识': 'bg-purple-600',
      '技术理论': 'bg-blue-600',
      '商务知识': 'bg-green-600'
    };
    return colors[type] || 'bg-gray-600';
  };

  // 获取复杂度颜色
  const getComplexityColor = (level) => {
    const colors = {
      '低': 'text-green-400',
      '中': 'text-yellow-400',
      '高': 'text-red-400'
    };
    return colors[level] || 'text-gray-400';
  };

  return (
    <div className="h-screen bg-gray-900 text-white flex flex-col overflow-hidden">
      {/* 顶部导航栏 */}
      <div className="bg-gray-800 border-b border-gray-700 px-6 py-4 flex-shrink-0">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={onBackToMain}
              className="flex items-center space-x-2 text-gray-300 hover:text-white transition-colors"
            >
              <ChevronRight className="w-4 h-4 rotate-180" />
              <span>返回主图</span>
            </button>
            <div className="flex items-center space-x-2">
              <BookOpen className="w-5 h-5 text-purple-400" />
              <h1 className="text-xl font-bold">知识测 - 体系化分组展示</h1>
            </div>
          </div>
          
          {/* 搜索和筛选 */}
          <div className="flex items-center space-x-4">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
              <input
                type="text"
                placeholder="搜索知识点..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 pr-4 py-2 bg-gray-700 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500 w-64"
              />
            </div>
            
            <div className="flex items-center space-x-2">
              <Filter className="w-4 h-4 text-gray-400" />
              <select
                value={selectedCategory}
                onChange={(e) => setSelectedCategory(e.target.value)}
                className="bg-gray-700 rounded-lg px-3 py-2 text-white focus:outline-none focus:ring-2 focus:ring-purple-500"
              >
                <option value="all">所有分组</option>
                {knowledgeCategories.map(cat => (
                  <option key={cat.chapter} value={cat.chapter}>
                    {cat.chapter} ({cat.node_count})
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>
      </div>

      {/* 主要内容区域 */}
      <div className="flex-1 overflow-y-auto p-6">
        <div className="max-w-6xl mx-auto">
          {/* 统计信息 */}
          <div className="grid grid-cols-4 gap-4 mb-6">
            <div className="bg-gray-800 rounded-lg p-4">
              <div className="text-2xl font-bold text-purple-400">{filteredNodes.length}</div>
              <div className="text-sm text-gray-400">总知识点</div>
            </div>
            <div className="bg-gray-800 rounded-lg p-4">
              <div className="text-2xl font-bold text-blue-400">{filteredCategories.length}</div>
              <div className="text-sm text-gray-400">知识域分组</div>
            </div>
            <div className="bg-gray-800 rounded-lg p-4">
              <div className="text-2xl font-bold text-green-400">
                {filteredCategories.reduce((sum, cat) => sum + cat.node_count, 0)}
              </div>
              <div className="text-sm text-gray-400">已分类知识点</div>
            </div>
            <div className="bg-gray-800 rounded-lg p-4">
              <div className="text-2xl font-bold text-yellow-400">
                {expandedGroups.size}
              </div>
              <div className="text-sm text-gray-400">展开分组</div>
            </div>
          </div>

          {/* 分组展示 */}
          <div className="space-y-4">
            {filteredCategories.map((category) => {
              const groupNodes = getGroupNodes(category);
              const isExpanded = expandedGroups.has(category.chapter);
              
              return (
                <div key={category.chapter} className="bg-gray-800 rounded-lg border border-gray-700">
                  {/* 分组标题 */}
                  <div 
                    className="flex items-center justify-between p-4 cursor-pointer hover:bg-gray-700 transition-colors"
                    onClick={() => toggleGroup(category.chapter)}
                  >
                    <div className="flex items-center space-x-3">
                      {isExpanded ? (
                        <ChevronDown className="w-5 h-5 text-purple-400" />
                      ) : (
                        <ChevronRight className="w-5 h-5 text-purple-400" />
                      )}
                      <div>
                        <h3 className="text-lg font-semibold text-white">{category.chapter}</h3>
                        <p className="text-sm text-gray-400">
                          {category.node_count} 个知识点 • {category.category}
                        </p>
                      </div>
                    </div>
                    <div className="flex items-center space-x-2">
                      <span className="text-sm text-gray-400">
                        {groupNodes.length} 个匹配
                      </span>
                    </div>
                  </div>

                  {/* 分组内容 */}
                  {isExpanded && (
                    <div className="border-t border-gray-700 p-4">
                      {groupNodes.length === 0 ? (
                        <div className="text-center py-8 text-gray-400">
                          <BookOpen className="w-12 h-12 mx-auto mb-4 opacity-50" />
                          <p>该分组中没有匹配的知识点</p>
                        </div>
                      ) : (
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                          {groupNodes.map((node) => (
                            <div 
                              key={node.id} 
                              className="bg-gray-700 rounded-lg p-4 hover:bg-gray-600 transition-colors border border-gray-600"
                            >
                              <div className="flex items-start justify-between mb-2">
                                <h4 className="font-medium text-white text-sm leading-relaxed">
                                  {node.name}
                                </h4>
                                <span className={`px-2 py-1 rounded text-xs font-medium ${getKnowledgeTypeColor(node.knowledge_type)}`}>
                                  {node.knowledge_type}
                                </span>
                              </div>
                              
                              <p className="text-xs text-gray-300 mb-3 line-clamp-2">
                                {node.description}
                              </p>
                              
                              <div className="flex items-center justify-between text-xs">
                                <span className={`font-medium ${getComplexityColor(node.complexity_level)}`}>
                                  复杂度: {node.complexity_level}
                                </span>
                                <span className="text-gray-400">
                                  ID: {node.id}
                                </span>
                              </div>
                              
                              {/* 学习路径 */}
                              {node.learning_path && node.learning_path.length > 0 && (
                                <div className="mt-3 pt-3 border-t border-gray-600">
                                  <div className="text-xs text-gray-400 mb-1">学习路径:</div>
                                  <div className="flex flex-wrap gap-1">
                                    {node.learning_path.slice(0, 3).map((step, idx) => (
                                      <span 
                                        key={idx}
                                        className="px-2 py-1 bg-purple-600/20 text-purple-300 rounded text-xs"
                                      >
                                        {step}
                                      </span>
                                    ))}
                                    {node.learning_path.length > 3 && (
                                      <span className="px-2 py-1 bg-gray-600 text-gray-300 rounded text-xs">
                                        +{node.learning_path.length - 3}
                                      </span>
                                    )}
                                  </div>
                                </div>
                              )}
                            </div>
                          ))}
                        </div>
                      )}
                    </div>
                  )}
                </div>
              );
            })}
          </div>

          {/* 空状态 */}
          {filteredCategories.length === 0 && (
            <div className="text-center py-12">
              <BookOpen className="w-16 h-16 mx-auto mb-4 text-gray-400" />
              <h3 className="text-lg font-medium text-gray-300 mb-2">没有找到匹配的知识点</h3>
              <p className="text-gray-400">请尝试调整搜索条件或筛选条件</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default KnowledgeTest; 