import React, { useState, useEffect } from 'react';

const GameSplash = ({ onEnter }) => {
  const [showEnterButton, setShowEnterButton] = useState(false);
  const [isExiting, setIsExiting] = useState(false);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    // 页面加载时的淡入效果
    setTimeout(() => setIsVisible(true), 100);
    
    // 5秒后显示进入按钮
    const timer = setTimeout(() => {
      setShowEnterButton(true);
    }, 5000);

    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    // 监听键盘按键
    const handleKeyPress = (event) => {
      if (showEnterButton) {
        handleEnter();
      }
    };

    // 监听鼠标点击
    const handleClick = () => {
      if (showEnterButton) {
        handleEnter();
      }
    };

    document.addEventListener('keydown', handleKeyPress);
    document.addEventListener('click', handleClick);

    return () => {
      document.removeEventListener('keydown', handleKeyPress);
      document.removeEventListener('click', handleClick);
    };
  }, [showEnterButton]);

  const handleEnter = () => {
    setIsExiting(true);
    // 淡出动画后进入主界面
    setTimeout(() => {
      onEnter();
    }, 500);
  };

  return (
    <div 
      className={`fixed inset-0 z-50 bg-gradient-to-br from-slate-900 via-blue-900 to-indigo-900 flex flex-col justify-between p-8 transition-all duration-500 ${
        isVisible && !isExiting ? 'opacity-100 scale-100' : 'opacity-0 scale-95'
      }`}
    >
      {/* 顶部区域 - 学校Logo */}
      <div className="relative -mx-8 -mt-8 mb-8">
        {/* 横向全宽背景条带 - 纯白色，从左到右贯通 */}
        <div className="absolute inset-x-0 top-0 h-20 bg-white shadow-lg"></div>
        <div className="relative flex justify-start py-2 px-12 z-10">
          <img 
            src="/know2/课件资源/学校logo.png" 
            alt="学校logo" 
            className="h-16 w-auto opacity-100 transition-opacity duration-1000 drop-shadow-lg"
            onError={(e) => {
              e.target.style.display = 'none';
            }}
          />
        </div>
      </div>

      {/* 中央区域 - 主要内容 */}
      <div className="flex-1 flex flex-col items-center justify-center text-center">
        {/* 课程标题 */}
        <div className="mb-8">
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold text-white mb-4 leading-tight transform transition-all duration-1000 delay-300">
            <span className="block">《跨境电商物流与全球供应链》</span>
            <span className="block text-3xl md:text-4xl lg:text-5xl text-blue-200 mt-2">课程研究知识图谱</span>
          </h1>
          
          {/* 作者名字 */}
          <div className="flex justify-end mt-6 pr-8">
            <span className="text-xl md:text-2xl text-blue-100 font-medium transform transition-all duration-1000 delay-500">孙静</span>
          </div>
        </div>

        {/* 进入按钮区域 */}
        <div className="mt-12">
          {showEnterButton ? (
            <div className="animate-fade-in">
              <button
                onClick={handleEnter}
                className="px-8 py-4 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white text-lg font-semibold rounded-xl shadow-lg transform transition-all duration-300 hover:scale-105 hover:shadow-xl animate-pulse"
              >
                点击进入知识图谱
              </button>
              
              {/* 提示文字 */}
              <p className="text-blue-200 text-sm mt-4 opacity-80">
                按任意键或点击鼠标进入
              </p>
            </div>
          ) : (
            <div className="animate-fade-in">
              <div className="flex items-center justify-center space-x-2 text-blue-200">
                <div className="w-2 h-2 bg-blue-400 rounded-full animate-bounce"></div>
                <div className="w-2 h-2 bg-blue-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                <div className="w-2 h-2 bg-blue-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                <span className="ml-2 text-lg font-medium">加载中...</span>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* 底部区域 - 课研AI标识 */}
      <div className="flex justify-end">
        <div className="text-blue-300 text-lg font-medium opacity-80 transform transition-all duration-1000 delay-700">
          Powered by 中物汇智 课研AI
        </div>
      </div>
    </div>
  );
};

export default GameSplash; 